body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.header-color {
  background: #11998e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* Landing Page */
.landing-grid {
  background: #11998e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.projects-grid {
  display: -webkit-flex;
  display: flex;
}

/* About me CSS */

.about-body{
  margin: auto;
  background: #11998e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
 
}

.about-grid {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 60%;
  height: 500px;
  background: white;
  color: black;
  border: 2px solid #a5d9b0;
  margin-bottom: 20px;
  border-radius: 10px;
}

/* resume  CSS*/
.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}


/* contacts CSS*/
.contact-body {
  margin: auto;
  background: #11998e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 2em;
}

.contact-grid{
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  border: 2px solid #a5d9b0;
  margin-bottom: 20px;
  border-radius: 10px;
}

.contact-grid h2 {
  font-family: 'Slabo 27px', serif;
}
.contact-grid p {
  font-family: 'PT Serif', serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted #01104b;
  width: 50%;
  margin: auto;
  font-family: 'PT Serif', serif;
}

.contact-list i {
font-size: 66px;
padding-right: 5em;
margin: -17px 0px -7px 0px;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px; 
}

.banner-text h1 {
  font-size: 56px;
  font-weight: bold;
  color: white;
  font-family: 'Droid Sans';
  padding-left: 37px;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
  /* font-weight: bold; */
  font-family: 'Droid Sans';
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.social-links i i i {
  color: blue;
}
